@font-face {
  font-family: "speed4courier";
  src: url("#{$baseUrl}asset/font/speed4courier.ttf?zd4i6s") format("truetype"),
    url("#{$baseUrl}asset/font/speed4courier.woff?zd4i6s") format("woff"),
    url("#{$baseUrl}asset/font/speed4courier.svg?zd4i6s#speed4courier")
      format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "speed4courier" !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

